import React from 'react'
import Helmet from 'react-helmet'
import NavigationMenu from '../../components/NavigationMenu/index.js'
import Footer from '../../components/Footer/index.js'

import "../../styles/main.scss";

class SimpleLayout extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      title: props.title,
      description: props.description,
      keywords: props.keywords,
      children: props.children,
    };
  }

  componentDidMount() {
    if (window.location.hash) {
      setTimeout(() => {
        document.querySelector(`${window.location.hash}`).scrollIntoView();
      }, 0);
    }
  }

  render() {

    return (
      <>
        <Helmet
          title={this.state.title}
          meta={[
            { name: 'description', content: this.state.description },
            { name: 'keywords', content: this.state.keywords },
          ]}
          
        >
          <html lang="fr" />
        </Helmet>
        <div className="bodywrapper">
          <NavigationMenu />
          <main className="page-content" role="main">
            {this.state.children}
          </main>
          {this.props.title!=='404' && this.props.title!=='Mentions'? <Footer/> : ''}
        </div>
      </>
    );
  }


}

export default SimpleLayout;